













































import {
  computed, defineComponent, PropType, toRefs,
} from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon';
import useI18N from '@/app/shared/client/logic/i18n';
import { LocationData } from '@/app/shared/client/type/location';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';

const LocationContentEditor = defineComponent({
  components: {
    ArrowRightIcon: Icon.ArrowRightIcon,
    PlusIcon: Icon.PlusIcon,
  },
  props: {
    label: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<LocationData>,
    },
  },
  setup(props, { root }) {
    const i18n = useI18N(root.$store);
    const { value: content } = toRefs(props);

    const locationButtonText = computed(() => {
      if (isNotNullOrEmpty(content?.value?.address)) {
        return content?.value?.address;
      }

      if (isNotNullOrEmpty(content?.value?.postalAddress)) {
        return content?.value?.postalAddress;
      }

      return undefined;
    });

    const hasLocationAddress = computed(() => {
      if (isNotNullOrEmpty(content?.value?.address)) {
        return true;
      }

      if (isNotNullOrEmpty(content?.value?.postalAddress)) {
        return true;
      }

      return false;
    });

    return {
      i18n,
      locationButtonText,
      hasLocationAddress,
    };
  },
});

export default LocationContentEditor;
